  font-weight: normal;
  font-style: normal;
}

/* === GLOBAL VARIABLES === */

:root {
  --dark-umber-color: hsl(0 11% 21%);
  --soft-gold-color: hsl(38 66% 71%);
  --deep-moss-green-color: hsl(127 28% 25%);
  --light-beige-color: hsl(36 56% 91%);
  --pale-cream-color: hsl(37 74% 90%);
  --brownish-red-color: hsl(0 59% 41%);
  --deep-aqua-blue-color: hsl(201 100% 36%);
  --light-icy-blue-color: hsl(187 72% 93%);
  --raspberry-color: hsl(0 79% 72%);
  --deep-navy-blue-color: hsl(210 100% 20%);
  --peach-color: hsl(28 100% 86%);
  --system-ui:
    system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

  --font-size-s: 0.875rem;
  --font-size-m: 1rem;
  --font-size-l: 1.125rem;
  --font-size-xl: 1.3125rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 2.652rem;
}

/* === BASE STYLES === */

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex-grow: 1;
}

button {
  cursor: pointer;
  border: none;
}

.container {
  width: 80%;
  margin: 0 auto;
}

/* === NAVBAR STYLES === */

.header {
  background: var(--dark-umber-color);
}

.navbar.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 1.5em 0;
  color: var(--soft-gold-color);
}

.brand {
  font-family: "Hanging Letters", var(--system-ui);
  font-size: var(--font-size-3xl);
}

.nav {
  display: flex;
  gap: 2em;
}

.button {
  background: transparent;
  padding: 1em 2em;
  color: var(--pale-cream-color);
  font-family: "Beauty Sunny", var(--system-ui);
  font-weight: bold;
  font-size: var(--font-size-xl);
  transition:
    background 0.4s ease,
    color 0.4s ease,
    border 0.4s ease,
    border-radius 0.4s ease;
}

.button:hover {
  border-radius: 0.5em;
  background: var(--soft-gold-color);
  color: var(--dark-umber-color);
  border-bottom: none;
}

.button.active {
  border-bottom: solid 0.1em var(--soft-gold-color);
}

/* === MAIN CONTENT STYLES === */

/* --- HOME PAGE --- */

.content {
  background: var(--deep-moss-green-color);
  color: var(--light-beige-color);
}

.home {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3em 0;
}

.content__left {
  display: flex;
  flex-direction: column;
  gap: 2em;
}

.content__title {
  font-family: "Beauty Sunny", var(--system-ui);
  font-size: var(--font-size-3xl);
}

.content__body {
  font-family: "Chicago", var(--system-ui);
  font-size: var(--font-size-xl);
}

.content__left,
.content__right {
  flex-basis: 40%;
}

/* --- MENU PAGE --- */

.food {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;
  border-radius: 0.5em;
  margin: 5em 0;
}

.line {
  width: 80%;
}

.hot-foods {
  background: var(--brownish-red-color);
}

.cool-drinks {
  background: var(--deep-aqua-blue-color);
  color: var(--light-icy-blue-color);
}

.desserts {
  background: var(--raspberry-color);
  color: var(--deep-navy-blue-color);
}

.food-title {
  padding-top: 1em;
  font-family: "Beauty Sunny", var(--system-ui);
  font-size: var(--font-size-2xl);
}

.hot-foods__type,
.cool-drinks__type,
.desserts__type {
  display: flex;
  justify-content: space-around;
  gap: 5em;
}

.menu__item-dish-name {
  padding: 1.5em 0;
  text-align: center;
  font-style: italic;
  font-family: "Chicago", var(--system-ui);
  font-weight: var(--font-size-2xl);
}

.menu__item-image {
  overflow: hidden;
  border-radius: 0.8em;
}

.menu__item-image img {
  max-width: 250px;
  aspect-ratio: 1/1;

  border-radius: 0.8em;
  transition: transform 0.4s ease;
}

.menu__item-image:hover img {
  cursor: pointer;
  transform: scale(1.2);
}

/* --- ABOUT PAGE --- */

.about {
  background: var(--pale-cream-color);
  color: var(--dark-umber-color);
  padding: 2em;
  border-radius: 0.5em;
  margin-top: 2em;
  font-family: "Chicago", var(--system-ui);
  font-size: var(--font-size-xl);
  margin-bottom: 2em;
}

.about__title {
  font-family: "Beauty Sunny", var(--system-ui);
  margin-bottom: 1em;
}

.about .line {
  width: 100%;
  background: var(--dark-umber-color);
  margin-bottom: 1em;
}

.about__contact {
  display: flex;
  gap: 2em;
}

.contact {
  border-left: solid 2px var(--dark-umber-color);
  padding-left: 2em;
}

/* === FOOTER STYLES === */

.footer {
  background: var(--dark-umber-color);
  color: var(--soft-gold-color);
  text-align: center;
  padding: 1em 0;
  font-family: "Beauty Sunny", var(--system-ui);
  font-weight: bold;
  font-size: var(--font-size-l);
  letter-spacing: 0.2em;
}

/* MEDIA QUERIES */

@media (width < 1000px) {
  .hot-foods__type,
  .cool-drinks__type,
  .desserts__type {
    flex-direction: column;
  }

  .menu__item-image img {
    max-width: 80%;
    margin: 0 auto;
    aspect-ratio: 16 / 9;
    object-fit: contain;
  }
}
